import React from "react"
import { DomsStyled } from "./Doms.styled"
import Doms from "./Doms"

class Index extends React.Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  render() {
    /*
     *
     * User "container" state/props/methods!
     * This "child" component is to simplify container's render() logic.
     *
     */
    let {
      domains_generic = [],
      domains_suggested = {},
      domains_availability = {},
      ui = {},
      input_tld = "",
      input_str = "",
      input_spellchecked = "",
      tlds_checked = [],
      input_words_arr = []
    } = this.props
    let suggestions_keys = ["exact", "tld", "com", "word hack", "phrase hack", "name"]
    let suggestions_lists = domains_suggested
    if (!suggestions_lists["exact"]) {
      suggestions_lists["exact"] = domains_generic
    }

    let render_list_keys = []
    let render_lists = {}

    /*
     *
     *
     * RENDER EACH INDIVIDUAL LIST
     *
     *
     */

    /*
     *
     * Filter lists
     *
     */
    if (!!ui.show_one_list) {
      render_list_keys = ["all"]
    } else {
      for (let listname of suggestions_keys) {
        if (listname === "word hack" && !ui.show_word_hacks) continue
        if (listname === "phrase hack" && !ui.show_phrase_hacks) continue
        render_list_keys.push(listname)
      }
    }

    /*
     *
     * Filter doms
     *
     */
    let all_doms = {}
    for (let listname of render_list_keys) {
      if (!suggestions_lists[listname]) continue
      // init list
      let list_pp = []
      let list_p = []
      let list = []
      // fill list
      for (let domStr of suggestions_lists[listname]) {
        let domStrNoSpaces = domStr.replace(/ /g, "")
        // unique! if already shown in previous list, don't show in any future list
        if (all_doms[domStrNoSpaces]) continue
        // show dom in list?
        let code = Number(domains_availability[domStrNoSpaces] || 0)
        if (
          (code >= 1 && code <= 1000) ||
          (code > 10000000 && !!ui.show_unavailable) ||
          code > 10000005 ||
          !code ||
          (listname === "exact" && domStrNoSpaces === input_str + "." + input_tld)
        ) {
          // ok, add now
          all_doms[domStrNoSpaces] = true
          list.push(domStr)
          //
        } else if (code > 1000 && code <= 10000 && !!ui.show_premium_cheap) {
          // premium, mix in after
          all_doms[domStrNoSpaces] = true
          list_p.push(domStr)
          //
        } else if (
          (code === 1501 && !!ui.show_premium_unknown) ||
          (code === 501 && !!ui.show_premium_unknown) ||
          (code > 10000 && code < 10000000 && !!ui.show_premium_expensive)
        ) {
          // premium, mix in after
          all_doms[domStrNoSpaces] = true
          list_pp.push(domStr)
          //
        }
      }
      // mix in premium
      let list_i = 0
      let p_n = 1
      let splice_freq = !!ui.show_one_list ? 4 : 3
      let splice_start = !!ui.show_one_list ? 0 : Math.floor(list.length / 10)
      for (let plist of [list_p, list_pp]) {
        for (let domStr of plist) {
          let int = p_n * (splice_freq + list_i) //Math.ceil(Math.random() * 4 + 0.5)
          if (p_n === 1) {
            int++
          }
          if (p_n % 2) {
            int++
          }
          if (!(p_n % 3)) {
            int += list_i
          }
          if (list.length > int) {
            list.splice(splice_start + int, 0, domStr)
          } else {
            list.push(domStr)
          }
          p_n++
        }
        list_i++
      }
      // save
      render_lists[listname] = list
    }

    /*
     *
     *
     * RENDER ALL RESULTS INTO ONE LIST
     *
     *
     */
    // if (!!ui.show_one_list) {
    //   render_list_keys = ["all"]
    //   let all = []
    //   let ratios = {
    //     "exact": 1,
    //     "tld": 2,
    //     "com": 3,
    //     "name": 4,
    //     "phrase hack": 3,
    //     "word hack": 2
    //   }
    //   for (let ln = 1; ln < 50; ln++) {
    //     for (let listname in ratios) {
    //       if (!render_lists[listname]) continue
    //       if (listname === "all") continue
    //       let ratio = ratios[listname]
    //       // from previous (which is current minus one) until current
    //       // first pass on "tld": from ((prev-1)*ratio) ((1-1)*3 = 0) until less than (current*ratio) (1*3 = 3)
    //       // secnd pass on "tld": from ((prev-1)*ratio) ((2-1)*3 = 3) until less than (current*ratio) (2*3 = 6)
    //       for (let li = (ln - 1) * ratio; li < ln * ratio; li++) {
    //         // add each item in range
    //         let dom = render_lists[listname][li]
    //         if (!dom) break
    //         all.push(dom)
    //       }
    //     }
    //   }
    //   render_lists = { all }
    // }

    /*
     *
     * View
     *
     */
    return (
      <DomsStyled className={"DomsStyled"}>
        <Doms
          domains_availability={domains_availability}
          suggestions_lists={render_lists}
          suggestions_keys={render_list_keys}
          input_tld={input_tld}
          input_str={input_str}
          input_spellchecked={input_spellchecked}
          ui={ui}
          tlds_checked={tlds_checked}
          input_words_arr={input_words_arr}
        />
      </DomsStyled>
    )
  }
}

export default Index
